import React from 'react';
import Tab from "@material-ui/core/Tab";
import Tabs from "@material-ui/core/Tabs";
import School from "./School/School";
import Collage from "./Collage/Collage";
import './LifeChapter.scss';

class LifeChapter extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            activeTab :0
        }
    }

    changeActiveTab = (event, activeTab) => {
        this.setState({ activeTab });
    };

    getTabContain = (activeTab,isMobile)=> {
        switch (activeTab) {
            case 0 :
                return <School isMobile={isMobile}/>;
            case 1:
                return <Collage isMobile={isMobile}/>;
            default :
                return '404 not Found';
        }
    };

    render(){
        const {activeTab} = this.state;
        const {isMobile} = this.props;
        return(
            <React.Fragment>
            <Tabs
                value={activeTab}
                indicatorColor="primary"
                textColor="primary"
                onChange={this.changeActiveTab}
                fullWidth
            >
                <Tab label="School" />
                <Tab label="Collage" />
            </Tabs>
                <div className="TabContainer">
                    {this.getTabContain(activeTab,isMobile)}
                </div>
            </React.Fragment>
        )
    }

}

export default LifeChapter;