import React from 'react';
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import AppBar from "@material-ui/core/AppBar";
import IconButton from '@material-ui/core/IconButton';
import KeyboardBackspace from '@material-ui/icons/KeyboardBackspace';
import {Link} from 'react-router-dom';


const ListHeader = (props)=> {
    return(
        <AppBar className={props.headerClass} position={props.position} color="default">
            <Toolbar>
                {
                  props.action && <Link style={{color:'#212121'}} to="/"><IconButton color="inherit" aria-label="Menu">
                        <KeyboardBackspace />
                  </IconButton></Link>
                }
                <Typography className={props.headerTextClass} variant="h6" color="inherit">
                    {props.headarText}
                </Typography>
            </Toolbar>
        </AppBar>
    )
};

export default ListHeader;