import React from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Hiren from "../../../../images/hiren.jpg";
import Avatar from "@material-ui/core/Avatar";
import PersonalProfiles from "../../../PersonalProfiles/PersonalProfiles";
import LifeChapter from "../../../LifeChapter/LifeChapter";
import ListHeader from "../../../ListHeader/ListHeader";
import {Link} from "react-router-dom";
import Button from "@material-ui/core/Button";
import MySkills from "../../../Skills/Skills";
import MyInterestedIn from "../../../MyIntresetedIn/MyIntresetedIn";
import MobileDialog from "../../../Dialogs/MobileDialog/MobileDialog";
import './HomepageTablet.scss';

class HomepageTablet extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            open : false,
        }
    }

    MobileDialog = (open)=> {
        this.setState({open});
    };

    render(){
        const {open} = this.state;
        return(
            <div>
                <Grid container spacing={16}>
                    <Grid item xs={12}>
                        <Paper className="HomeCard">
                            <PersonalProfiles parentContainerClass="containerSetting" profileClass="PersonalProfilesDesktop" xs={2}/>
                            <Avatar onClick={()=>{this.MobileDialog(true)}} className="profileTablet" alt="hiren tariwala" src={Hiren}/>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="ProfileNameCard">
                            <div className="profileNameTablet">Hiren Tariwala</div>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="LifeChapter" square>
                            <LifeChapter isMobile={false}/>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="LifeChapter" square>
                            <ListHeader position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="My Skills"/>
                            <MySkills/>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="LifeChapter" square>
                            <ListHeader  position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="Interested In"/>
                            <MyInterestedIn/>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="LifeChapter" square>
                            <ListHeader  position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="ReactJs Example"/>
                            <div className="exampleContainer">
                                <Link className="btnLink" to="/example/todoListApp"> <Button className="exampleBtn" variant="contained" color="primary">
                                    Todo List
                                </Button>
                                </Link>
                                <Link className="btnLink" to="/example/userCrudApp">
                                    <Button className="exampleBtn"  variant="contained" color="secondary">
                                        User Crud
                                    </Button>
                                </Link>
                            </div>
                        </Paper>
                    </Grid>
                </Grid>
                <MobileDialog open={open} onClose={()=>{this.MobileDialog(false)}}>
                    <Avatar className="profilePicDialog" alt="hiren tariwala" src={Hiren}/>
                </MobileDialog>
            </div>
        )
    }

}

export default HomepageTablet;