import React from 'react';
import ListHeader from "../ListHeader/ListHeader";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import DoneIcon from "@material-ui/icons/Done";
import ClearIcon from "@material-ui/icons/Clear";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";
import MobileDialog from "../Dialogs/MobileDialog/MobileDialog";
import AddIcon from "@material-ui/icons/Add";
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import './UserCrudApp.scss';

class UserCrudApp extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            deleteModal: false,
            userDetailsModal: false,
            userList: [
                {firstName: 'Hiren', lastName: 'Tariwala', city: 'Surat'}
            ],
            selectedUserIndex: null,
            selectedUser: null,
            isEdit : false,
            newUser:{firstName: '', lastName: '', city: ''}
        }
    };

    deleteModalDialog = (open,index)=> {
        this.setState({deleteModal:open,selectedUserIndex:index});
    };

    userDetailsDialog = (open,index,selectedUser,isEdit)=> {
        this.setState({userDetailsModal:open,selectedUserIndex:index,selectedUser,isEdit,newUser:{firstName: '', lastName: '', city: ''}});
    };

    deleteUser = ()=> {
        const {userList,selectedUserIndex} = this.state;
        userList.splice(selectedUserIndex,1);
        this.setState({userList,selectedUserIndex:null,deleteModal:false});
    };

    onAddUser = ()=> {
        const {userList,newUser} = this.state;
        userList.push(newUser);
        this.setState({userList,newUser:{firstName: '', lastName: '', city: ''},userDetailsModal:false});
    };

    onEditUser = ()=> {
        const {userList,selectedUser,selectedUserIndex} = this.state;
        Object.keys(selectedUser).forEach((key)=>{ userList[selectedUserIndex][key] = selectedUser[key] });
        this.setState({userList,selectedUserIndex:null,selectedUser:null,isEdit:false,userDetailsModal:false});
    };

    onChangeText = (key,value,isEdit)=>{
      isEdit ? this.setState((prevState)=>({...prevState,selectedUser:{...prevState.selectedUser,[key]: value.trim()}})) :  this.setState((prevState)=>({...prevState,newUser:{...prevState.newUser,[key]: value.trim()}}));
    };

    handleChange = (e,isEdit)=>{
      isEdit ? this.setState((prevState)=>({...prevState,selectedUser:{...prevState.selectedUser,city:e.target.value.trim()}}))  :  this.setState((prevState)=>({...prevState,newUser:{...prevState.newUser,city:e.target.value.trim()}}));
    };

    isDisable = (isEdit)=> {
        const {selectedUser,newUser} = this.state;
        switch (isEdit) {
            case true :
                return !selectedUser.firstName || !selectedUser.lastName || !selectedUser.city;
            default :
                return !newUser.firstName || !newUser.lastName || !newUser.city;
        }
    };

    render(){
        const {selectedUser,userList,deleteModal,userDetailsModal,newUser,isEdit} = this.state;
        const {parent} = this.props;
        return(
            <div className={!parent && 'appBody'}>
                { !parent && <ListHeader action position="fixed" headerTextClass="customFontAttribute" headarText="UserCrud App"/> }
                <Grid  container spacing={8}>
                    <Grid item xs={12}>
                        <Paper className="LifeChapter" square>
                            <ListHeader position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="UserList"/>
                            <Table>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>FirstName</TableCell>
                                        <TableCell>LastName</TableCell>
                                        <TableCell>City</TableCell>
                                        <TableCell>Edit</TableCell>
                                        <TableCell>Delete</TableCell>
                                    </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {
                                            userList.map((user,i)=>{
                                                return(
                                                    <TableRow key={i}>
                                                        <TableCell>{user.firstName}</TableCell>
                                                        <TableCell>{user.lastName}</TableCell>
                                                        <TableCell>{user.city}</TableCell>
                                                        <TableCell className="makeLink" onClick={()=>{this.userDetailsDialog(true,i,user,true)}}><EditIcon/></TableCell>
                                                        <TableCell className="makeLink" onClick={()=>{this.deleteModalDialog(true,i)}}><DeleteIcon/></TableCell>
                                                    </TableRow>
                                                )
                                            })
                                        }
                                    </TableBody>
                            </Table>
                        </Paper>
                    </Grid>
                    <Grid item xs={12}>
                        { parent && <Button onClick={()=>{this.userDetailsDialog(true,null,null,false)}} variant="contained" color="primary"> <AddIcon/> Add User </Button> }
                    </Grid>
                </Grid>
                <MobileDialog open={deleteModal} onClose={()=>{this.deleteModalDialog(false,null)}}>
                    <div>Are you sure you want to delete this user ?</div>
                    <Button onClick={this.deleteUser} variant="contained" color="secondary"><DoneIcon/></Button>
                    <Button onClick={()=>{this.deleteModalDialog(false,null)}} style={{margin:'15px'}} variant="contained" color="primary"><ClearIcon/></Button>
                </MobileDialog>
                <MobileDialog open={userDetailsModal} onClose={()=>{this.userDetailsDialog(false,null)}}>
                    <div style={{display:'grid'}}>
                        <TextField
                            value={ selectedUser ? selectedUser.firstName : newUser.firstName}
                            label="FirstName"
                            margin="normal"
                            onChange={(e)=>{this.onChangeText('firstName',e.target.value,isEdit)}}
                        />
                        <TextField
                            value={ selectedUser ? selectedUser.lastName : newUser.lastName}
                            label="LastName"
                            margin="normal"
                            onChange={(e)=>{this.onChangeText('lastName',e.target.value,isEdit)}}
                        />
                        <Select
                            style={{marginTop:'25px'}}
                            value={ selectedUser ? selectedUser.city : newUser.city}
                            onChange={(e)=>{this.handleChange(e,isEdit)}}
                            displayEmpty
                        >
                            <MenuItem value="">Select City</MenuItem>
                            <MenuItem value="Surat">Surat</MenuItem>
                            <MenuItem value="Bardoli">Bardoli</MenuItem>
                            <MenuItem value="Navsari">Navsari</MenuItem>
                        </Select>
                    </div>
                    <Button onClick={ isEdit ? this.onEditUser : this.onAddUser} disabled={this.isDisable(isEdit)} variant="contained" color="primary">Save</Button>
                    <Button onClick={()=>{this.userDetailsDialog(false,null,null,false)}} style={{margin:'20px'}} variant="contained" color="secondary">Cancel</Button>
                </MobileDialog>
                { !parent && <Button onClick={()=>{this.userDetailsDialog(true,null,null,false)}} className="addTodoListBtn" variant="fab" color="primary">
                    <AddIcon/>
                </Button> }
            </div>
        )
    }
}

export default UserCrudApp;