import React from 'react';
import ItemsList from "../ItemList/ItemList";
import List from "@material-ui/core/List";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import ListHeader from "../ListHeader/ListHeader";
import MobileDialog from "../Dialogs/MobileDialog/MobileDialog";
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from '@material-ui/icons/Delete';
import DoneIcon from '@material-ui/icons/Done';
import './TodoListApp.scss';

class TodoListApp extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            todoList : [
                {text:'Todo 1',completed:false},
                {text:'Todo 2',completed:false}
            ],
            open:false,
            newTodo:'',
        }
    }

    AddTodoListDialog = (open)=> {
        this.setState({open,newTodo:''})
    };

    onAddTodoList = ()=> {
        const {todoList,newTodo} = this.state;
        todoList.push({text:newTodo,completed:false});
        this.setState({open:false,todoList,newTodo:''});
    };

    onChangeNewTodo = (value)=> {
        this.setState({newTodo:value.trim()});
    };

    completeTodo = (index,e)=> {
        const {todoList} = this.state;
        todoList[index].completed = !todoList[index].completed;
        this.setState({todoList});
        e.stopPropagation();
    };

    clearAllTodo = ()=> {
        this.setState({todoList:[]});
    };

    clearCompleted = ()=> {
        let {todoList} = this.state;
        todoList = todoList.filter((item) => !item.completed);
        this.setState({todoList});
    };

    render(){
        const {todoList,open,newTodo} = this.state;
        const {parent} = this.props;
        return(
            <div className={!parent && 'appBody'}>
            { !parent && <ListHeader action position="fixed" headerTextClass="customFontAttribute" headarText="TodoList App"/> }
            <Grid  container spacing={8}>
                <Grid item xs={12}>
                    <Paper className="LifeChapter" square>
                        <ListHeader position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="TodoList"/>
                        <List dense>
                            <ItemsList onCheck={this.completeTodo} checkbox items={todoList}/>
                        </List>
                    </Paper>
                </Grid>
                <Grid item xs={12}>
                    <div>
                        { parent && <Button onClick={()=>{this.AddTodoListDialog(true)}} variant="contained" color="primary"> <AddIcon/> Add Todo </Button> }
                        <Button size="small" style={{marginLeft: parent ? '20px' : '0px'}} disabled={!todoList.length} onClick={this.clearAllTodo} variant="contained" color="secondary" >
                          <DeleteIcon/> Clear All
                        </Button>
                        <Button size="small" style={{margin:'20px'}} disabled={!todoList.length || !todoList.filter((item)=> item.completed ).length} onClick={this.clearCompleted} variant="contained" color="primary" >
                           <DoneIcon/> Clear Completed
                        </Button>
                    </div>
                </Grid>
            </Grid>
                <MobileDialog open={open} onClose={()=>{this.AddTodoListDialog(false)}}>
                    <div style={{display:'grid'}}>
                    <TextField
                        label="Add new todo"
                        margin="normal"
                        value={newTodo}
                        onChange={(e)=>{this.onChangeNewTodo(e.target.value)}}
                    />
                    <Button disabled={!newTodo} onClick={this.onAddTodoList} variant="contained" color="primary" >
                        Add
                    </Button>
                    </div>
                </MobileDialog>
                {!parent && <Button onClick={() => {
                    this.AddTodoListDialog(true)
                }} className="addTodoListBtn" variant="fab" color="primary">
                    <AddIcon/>
                </Button>
                }
            </div>
        )
    }
}

export default TodoListApp;