import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Homepage from './components/Homepage/Homepage';
import TodoListApp from './components/TodoListApp/TodoListApp';
import UserCrudApp from './components/UserCrudApp/UserCrudApp'
import * as serviceWorker from './serviceWorker';
import {BrowserRouter,Route,Switch} from 'react-router-dom';


ReactDOM.render(<BrowserRouter>
    <Switch>
        <Route exact path="/example/todoListApp" component={TodoListApp}/>
        <Route exact path="/example/userCrudApp" component={UserCrudApp}/>
        <Route exact path="/" component={Homepage}/>
    </Switch>
</BrowserRouter>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
