import React from 'react';
import Grid from "@material-ui/core/Grid";
import './PersonalProfiles.scss';

class PersonalProfiles extends React.Component{

    constructor(props){
        super(props);
        this.state = {
            profiles :[
                {iconClass:"fab fa-facebook-f",link:"https://www.facebook.com/hirentariwala19",fontClass:"font-facebook"},
                {iconClass:"fab fa-twitter",link:"https://twitter.com/hirentariwala",fontClass: "font-twitter"},
                {iconClass:"fab fa-instagram",link:"https://www.instagram.com/hirentariwala/",fontClass: "font-instagram"},
                {iconClass:"fab fa-linkedin",link:"https://www.linkedin.com/in/hirentariwala/",fontClass: "font-linkedin"},
                {iconClass:"fab fa-github",link:"https://github.com/HirenTariwala",fontClass: "font-github"},
                {iconClass:"fab fa-stack-overflow",link:"https://stackoverflow.com/users/10163879/hiren-tariwala",fontClass: "font-stack-overflow"},
            ]
        }
    }

    render(){
        const {profileClass,xs,parentContainerClass} = this.props;
        const {profiles} = this.state;
        return(
            <Grid container className={parentContainerClass} spacing={8}>
                {
                    profiles.map((profile,i)=>{
                        return(
                            <Grid key={i} item className={profileClass} xs={xs}>
                               <a className={profile.fontClass} href={profile.link}><i className={profile.iconClass}></i></a>
                            </Grid>
                        )
                    })
                }
            </Grid>
        )
    }

}

export default PersonalProfiles;