import React from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Hiren from '../../../images/hiren.jpg';
import PersonalProfiles from '../../PersonalProfiles/PersonalProfiles';
import ListHeader from "../../ListHeader/ListHeader";
import LifeChapter from "../../LifeChapter/LifeChapter";
import TodoListApp from "../../TodoListApp/TodoListApp";
import Tab from "@material-ui/core/Tab";
import Tabs from "@material-ui/core/Tabs";
import UserCrudApp from "../../UserCrudApp/UserCrudApp";
import MySkills from "../../Skills/Skills";
import MyInterestedIn from "../../MyIntresetedIn/MyIntresetedIn";
import './HomepageDesktopDevice.scss';

class HomepageDesktopDevice extends React.Component{

    constructor(props) {
        super(props);
        this.state = {
            activeTabExample : 0
        }
    }

    getTabContain = (activeTabExample)=> {
        switch (activeTabExample) {
            case 0 :
                return <TodoListApp parent={true}/>;
            case 1 :
                return <UserCrudApp parent={true}/>;
            default :
                return '404 not found';
        }
    };

    changeActiveTabExample = (event, activeTabExample) => {
        this.setState({ activeTabExample });
    };

    render(){
        const {activeTabExample} = this.state;
        return(
            <div>
                <Grid container spacing={8}>
                    <Grid item xs={12}>
                        <Paper className="HomeCard">
                            <PersonalProfiles profileClass="PersonalProfilesDesktop" xs={2}/>
                            <Avatar className="profilePicDesktop" alt="hiren tariwala" src={Hiren}/>
                        </Paper>

                    </Grid>
                    <Grid item xs={12}>
                        <Paper className="HomeCard">
                            <div className="profileNameDesktop">Hiren Tariwala</div>
                        </Paper>
                    </Grid>
                    <Grid item xs={6}>
                        <Paper className="LifeChapter" square>
                            <ListHeader position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="My Skills"/>
                           <MySkills/>
                        </Paper>
                    </Grid>
                    <Grid item xs={6}>
                        <Paper className="LifeChapter" square>
                            <LifeChapter isMobile={false}/>
                        </Paper>
                        <Paper style={{marginTop:'8px'}} className="LifeChapter" square>
                            <ListHeader  position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="Interested In"/>
                            <MyInterestedIn/>
                        </Paper>
                        <Paper style={{marginTop:'8px'}} className="LifeChapter" square>
                            <ListHeader  position="static" headerTextClass="customFontAttribute" headerClass="customHeader" headarText="ReactJs Example"/>
                            <Tabs
                                value={activeTabExample}
                                indicatorColor="primary"
                                textColor="primary"
                                onChange={this.changeActiveTabExample}
                                fullWidth
                            >
                                <Tab label="TodoList App" />
                                <Tab label="UserCrud App" />
                            </Tabs>
                            <div className="TabContainer">
                                { this.getTabContain(activeTabExample) }
                            </div>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        )
    }

}

export default HomepageDesktopDevice;